// Directivas: @mixin, @forward, @use, @include...
@use 'base/variables' as v; // Como no están en la misma ruta se usa la ruta absoluta
@use 'base/mixins' as m;

.header {
  background-color: v.$verde;

  @include m.tablet {
    &.fijo {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      box-shadow: .1rem .2rem .3rem #333;
    }
  }



  h1 {
    color: v.$blanco;
    text-align: center;

    @include m.tablet {
      margin: 0;
    }
  }

  .contenido-header {
    padding: 2rem;

    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}

// El ampersand "&" elimina el espacio de separacion entre a :hover quedando a:hover en el file compilado
.navegacion-principal {
  @include m.tablet {
    display: flex;
    gap: 2rem;
  }

  a {
    display: block;
    text-align: center;
    color: v.$blanco;
    font-size: 2.4rem;
    margin-bottom: 1rem;

    @include m.tablet {
      margin: 0;
    }

    &:hover {
      color: v.$amarillo;
    }
  }
}