@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos {
  padding: 5rem 0;

  h3 {
    text-align: center;
    color: v.$blanco;
    margin-bottom: 2rem;
  }

  .contenedor-boletos {
    @include m.tablet {
      @include m.grid(2, 5rem);
    }
  }
}

.pase {
  color: #000;

  .nombre-pase {
    text-align: center;
    font-size: 2.8rem;
    color: v.$blanco;
  }

  &-header,
  &-footer {
    padding: .5rem;

    p {
      text-align: center;
      color: v.$blanco;
      margin: 0;
    }
  }

  // .pase-header: se le agrega el ampersand y agregaria el nombre del padre
  &-header {
    background-color: v.$rosa;

    p {
      text-transform: uppercase;
      font-size: 1.6rem;
    }
  }

  // .pase-body: se le agrega el ampersand y agregaria el nombre del padre
  &-body {
    background-color: v.$blanco;
    ul {
      @include m.resetear-lista;
      li,
      :nth-child(odd) {
        background-color: v.$blanco;
        padding: 1rem;
        border-bottom: 1px solid #e1e1e1;
        font-size: 1.5rem;
      }
    }
  }

  // .pase-footer: se le agrega el ampersand y agregaria el nombre del padre
  &-footer {
    background-color: v.$morado;
    p {
      font-size: 2.4rem;
      font-weight: 700;
    }
  }
}